import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../../layouts/toast-notification/toast.component';
import { GlobalValidate } from '../../../../../../_services/validate.service';
import 'rxjs/add/operator/filter' ;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./configure-activity.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ConfigActivityComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;
 
configactivityForm: FormGroup;
 ajaxData : ConfigActivityData[];
configactivity= new ConfigActivityData();

public status ="";
public configSalesOpprName="";
msg = [];
	 
bootstrapFromValues = [];

constructor( private http: HttpClient, private _router: Router, private route:ActivatedRoute, private globalValid: GlobalValidate) {
		this.status = "Update";
		this.populateData();
	}
	
    ngOnInit() {
		this.configactivityForm = new FormGroup({
			'configSalesLeads' : new FormControl(''),
			'configSalesOppr' : new FormControl(''),
//			'configSalesOpprName' : new FormControl(''),
			'configSalesQuote' : new FormControl(''),
			'configSalesSalesOrder' : new FormControl(''),
			'configSalesInvoice' : new FormControl(''),
			'configSalesBalancePayments' : new FormControl(''),
			'configSalesCampaign' : new FormControl(''),
			'configSalesTarget' : new FormControl(''),
			'configSalesTeams' : new FormControl(''),
			'configSalesSOE' : new FormControl(''),
			'configSalesSOB' : new FormControl(''),
			'configSalesLeadForOppr' : new FormControl(''),
			'configSalesOpprForQuote' : new FormControl(''),
			'configSalesQuoteForSO' : new FormControl(''),
		});
	}
	  	get configSalesLeads(){ return this.configactivityForm.get('configSalesLeads')};
		get configSalesOppr(){ return this.configactivityForm.get('configSalesOppr')};
// 		get configSalesOpprName(){ return this.configactivityForm.get('configSalesOpprName')};
		get configSalesQuote(){ return this.configactivityForm.get('configSalesQuote')};
		get configSalesSalesOrder(){ return this.configactivityForm.get('configSalesSalesOrder')};
		get configSalesInvoice(){ return this.configactivityForm.get('configSalesInvoice')};
		get configSalesBalancePayments(){ return this.configactivityForm.get('configSalesBalancePayments')};
		get configSalesCampaign(){ return this.configactivityForm.get('configSalesCampaign')};
		get configSalesTarget(){ return this.configactivityForm.get('configSalesTarget')};
		get configSalesTeams(){ return this.configactivityForm.get('configSalesTeams')};
		get configSalesSOE(){ return this.configactivityForm.get('configSalesSOE')};
		get configSalesSOB(){ return this.configactivityForm.get('configSalesSOB')};
		get configSalesLeadForOppr(){ return this.configactivityForm.get('configSalesLeadForOppr')};
		get configSalesOpprForQuote(){ return this.configactivityForm.get('configSalesOpprForQuote')};
		get configSalesQuoteForSO(){ return this.configactivityForm.get('configSalesQuoteForSO')};
	 

	 populateData(){
			const headers = new HttpHeaders({'Content-Type':'application/json'});
			this.http.post<ConfigActivityData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/configactivity-data ',
				{ headers, withCredentials: true }).subscribe(data =>{
													this.configactivity = data['populatadata'][0];
													/*console.log("configModifiedId=="+this.configactivity.configModifiedId);*/
													},
										 (err) => {
												/* this function is executed when there's an ERROR */
												console.log("ERROR: "+err);
										 });
	 }
	 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/manager']);      
		 }

 		getCheckValues(temp:string){
			return this.globalValid.getCheckValue(temp);
		}
 
	  	onSubmit() {
			/*this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.configactivity[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}*/
			if(!this.configactivityForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});

				this.configactivity['requestType'] = 'update';
//				this.configactivity['configId'] = this.configId+'';

				this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/configactivity',
									JSON.stringify(this.configactivity),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.configactivityForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.configactivity);
	  }
 
	
}

	
	
export class ConfigActivityData {
	 configSalesLeads: string;
	configSalesOppr: string;
//	configSalesOpprName: string;
	configSalesQuote: string;
	configSalesSalesOrder: string;
	configSalesInvoice: string;
	configSalesBalancePayments: string;
	configSalesCampaign: string;
	configSalesTarget: string;
	configSalesTeams: string;
	configSalesSOE: string;
	configSalesSOB: string;
	configSalesLeadForOppr: string;
	configSalesOpprForQuote: string;
	configSalesQuoteForSO: string;
}


